import { BASE_URL_EACCOUNT } from "../contanst"

export const loginApi = async (body: any) => {
  const respones = await fetch(BASE_URL_EACCOUNT + 'auth/login',
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(body)
    }
  )
    .then((res) => res.json())
    .catch((e) => console.log(e))

  return respones;
}

export const forgetPassword = async (body: any) => {
  const respones = await fetch(BASE_URL_EACCOUNT + '',
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(body)
    }
  )
    .then((res) => res.json())
    .catch((e) => console.log(e))

  return respones;
}